@import url(https://fonts.googleapis.com/css?family=EB+Garamond&display=swap);
header{display:flex;justify-content:space-between;align-items:center}header h1,header h3{text-align:center}@media only screen and (max-width: 640px){header{flex-direction:column}}.first-name,.last-name{white-space:nowrap}.profile-links{padding:0 15px}.profile-links li{padding:5px 10px;white-space:nowrap}.profile-links span{margin-right:1em;font-size:22px}.job-title{color:#aa9d9d;margin-top:10px}
.skills-section h4{display:none}.skills-section .used-technologies-list{display:grid}.skills-section .used-technologies-list ul{list-style-type:square}.skills-section .used-technologies-list li{position:relative;width:20%;padding-left:1rem}.skills-section .used-technologies-list li:before{position:absolute;content:"";display:list-item;list-style-type:disc}@media only screen and (max-width: 640px){.skills-section .used-technologies-list li{width:40%}}
.education-details{margin-bottom:1rem}.education-details:last-child{margin-bottom:0}
.experience-item{line-height:1.5rem}.experience-item .experience-title{font-size:1.2rem;font-weight:500}.experience-item .company-name{font-size:.9rem}
.personal-projects-item{margin:0 0 2rem 0}.personal-projects-item:last-child{margin:0}.project-description{margin-bottom:1rem}.project-link .github-icon{font-size:32px}.project-link .github-icon{margin-right:10px}
@media print{body{background-color:#fff !important}.resume{box-shadow:none !important}}*{margin:0;padding:0}h1,h2,h3,h4,h5,h6{margin:1rem 0 .1rem 0;text-transform:capitalize}body{font-family:"EB Garamond",serif;background-color:#add8e6}ul{list-style:none}a{text-decoration:none;color:#123456}section{padding:10px 0}P:first-letter{text-transform:capitalize}.resume{max-width:700px;margin:0 auto;background-color:#fff;padding:1rem 2rem;box-shadow:0 10px 20px #a19696}.experience-item .tech-list-item span,.personal-projects-item .tech-list-item span{background-color:#505050;color:#fff;padding:2px 5px;border-radius:10px;font-size:14px;text-transform:capitalize}.used-technologies-list{page-break-inside:avoid}.section-title{position:relative}.section-title::after{position:absolute;display:block;content:"";width:100%;height:1px;right:0;bottom:0;background-color:#d3d3d3}.inline-list li{display:inline-block;margin:5px 5px}.time-span{color:#aab4c3}
